footer {
  color: #545454;
  background-color: rgb(247, 247, 247);

}

p {
  padding: 0;
  margin: 0;
}

.footer__icon {
  margin-left: 10px;
  margin-right: 10px;
}

@media screen and (min-width: 320px) {
  .copyright {
    margin: 0px;
    display: flex;
    justify-content: center;
    padding-top: 29px;
    padding-bottom: 10px;
  }

  .developers {
    margin: 0px;
    display: flex;
    justify-content: center;
    align-items: baseline;
    padding-bottom: 30px;
  }

  .page__footer {
    color: #545454;
    font-size: 14px;
    font-weight: 400;
    justify-content: center;
    align-items: baseline;
  }
}

@media screen and (min-width: 768px) {
  .page__footer {
    display: flex;
    justify-content: center;
    color: #545454;
  }

  .copyright {
    padding-top: 30px;
    padding-bottom: 28px;
  }

  .developers {
    padding-top: 30px;
    padding-bottom: 28px;
  }

  .page__footer {
    color: #545454;
    font-size: 16px;
    font-weight: 400;
  }
}

.copyright {
  color: #545454;
}

.developers {
  color: #545454;
}

.heart-icon {
  display: inline-flex;
  justify-content: center;
  align-items: flex-end;
  width: 1.25em;
  height: 1.2em;
  background-color: #0077ff;
  border-radius: 50%;
}

.films-footer__text {
  display: inline-block;
  padding: 5px 0;
}

.films-footer {
    
    background:  var(--color-background-footer);
    max-width: 100%;
    text-align: center;
font-family: 'Roboto';
    color: var(--color-text-footer);

  }

