.gallery__list {
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  
}

.gallery__item {
  width: 100%;
  @media screen and (min-width: 768px) {
    width: calc((100% - 30px) / 2);
  }
  @media screen and (min-width: 1280px) {
    width: calc((100% - 60px) / 3);
  }
}
.film_title {
  font-family: 'Roboto';
  font-weight: 500;
  font-size: 12px;
  line-height: calc(16 / 12);
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}
.film_text {
  color: #ff6b08;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: calc(16 / 12);

  text-align: left;
  @media screen and (min-width: 768px) {
  }
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}
.img {
  height: 400px;
  width: 280px;
  display: block;
  border-radius: 5px;
  margin-bottom: 10px;

  width: 100%;
  @media screen and (min-width: 768px) {
    width: 340px;
    height: 455px;
  }
  @media screen and (min-width: 1280px) {
    width: 400px;
    height: 570px;
  }
}
.film_text_border{
  border-right: 1px solid ;
  padding-right: 5px;
  @media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: calc(24 / 20);
  }
}